宝产业链中的所有商业活动和所有会员。RJC体系要求所有商业会员必须经过RJC授权的第三方认证机构的审核并通过其认证，以证明该会员的商业行为与RJC体系要求的行为规范是一致的。')}})]),_c('div',{staticClass:"text_div",attrs:{"data-v-2152299d":""}},[_c('span',{attrs:{"data-v-2152299d":""}},[_vm._v("了解更多")]),_c('img',{attrs:{"src":"data:image/png;base64,","alt":"图片"}})])])]),_c('div',{staticClass:"Certification_fors"},[_c('div',[_c('div',{staticClass:"certiscation_img"},[_c('img',{attrs:{"src":"https://vmcs.oss-cn-shenzhen.aliyuncs.com/project_img/1695107959471.png","alt":""}})]),_c('i'),_c('p',[_vm._v("IETP(ICTI)")]),_c('div',{staticClass:"div_html"},[_c('p',{domProps:{"innerHTML":_vm._s('&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp      ICTI认证， ICTI是国际玩具业协会的简称，英文全称：International Council of Toy Industries，成立于1974年，包括20个国家和地区的玩具贸易协会，遍布欧美及亚太区的主要玩具市场和玩具制造国家。ICTIP建立了 玩具业责任规范（IETP），IETP 是全球玩具行业的道德供应链项目。IETP 就全球的玩具制造定立了一套统一的道德采购标准， 获得世界各地玩具品牌与零售商的重用。 IETP 的认证在减少重复审核导致的消耗方面责任重大 -- 认证能有效减少因玩具品牌分别要求进行独立审核而致的重复 ICTI与玩具工厂、 零售商、 进口商及品牌商合作， 共同确保世界各地的玩具工人在安全及公平的环境下工作。IETP 根据 ICTI 商业行为守则列明的道德要求， 为玩具厂进行审核及认证。玩具买家通过加入 IETP 以向认证工厂采购玩具， 并参与ICTI的倡议企划， 共同改善玩具工人的福祉。玩具工厂加入 IETP 以获取认证， 且享用ICTI提供的培训、 教育及支援， 以改善工厂的劳动标准及持续提升业务水平。IETP 认证是众多买家信赖的卓越标志： 全球超过三分之二的玩具皆由 IETP 认证玩具工厂生产。随着IETP认证审核的成功完成，工厂将被授予IETP认证证书，以表明其符合上述守则的条款。')}})]),_c('div',{staticClass:"text_div",attrs:{"data-v-2152299d":""}},[_c('span',{attrs:{"data-v-2152299d":""}},[_vm._v("了解更多")]),_c('img',{attrs:{"src":"data:image/png;base64,","alt":"图片"}})])])]),_c('div',{staticClass:"Certification_fors"},[_c('div',[_c('div',{staticClass:"certiscation_img"},[_c('img',{attrs:{"src":"https://vmcs.oss-cn-shenzhen.aliyuncs.com/project_img/1695108015636.png","alt":""}})]),_c('i'),_c('p',[_vm._v("FJATA")]),_c('div',{staticClass:"div_html"},[_c('p',{domProps:{"innerHTML":_vm._s('&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp         FJATA 成立于 2005 年，此前在州、联邦和国际层面引入了大量相互冲突且经常重复的法规。这些法规对产品施加了限制，尽管缺乏适当的科学测试。缺乏科学支持的监管给制造商和进口商带来了不必要的成本和负担，禁止他们提供最终没有危险的商品。FJATA创始目标是创建一个在立法过程中代表零售商的组织。FJATA首先鼓励创建基于科学的标准，因此确保只有有正当理由限制的商品才受到限制。在这些努力中，FJATA协助美国材料与测试协会 （ASTM） 于 2011 年制定了《儿童珠宝消费品安全标准规范》，并于 2013 年制定了《成人珠宝标准消费者安全规范》。')}})]),_c('div',{staticClass:"text_div",attrs:{"data-v-2152299d":""}},[_c('span',{attrs:{"data-v-2152299d":""}},[_vm._v("了解更多")]),_c('img',{attrs:{"src":"data:image/png;base64,","alt":"图片"}})])])]),_c('div',{staticClass:"Certification_fors"},[_c('div',[_c('div',{staticClass:"certiscation_img"},[_c('img',{attrs:{"src":"https://vmcs.oss-cn-shenzhen.aliyuncs.com/project_img/1695108062307.png","alt":""}})]),_c('i'),_c('p',[_vm._v("FLA")]),_c('div',{staticClass:"div_html"},[_c('p',{domProps:{"innerHTML":_vm._s('&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp        FLA认证，即公平劳工协会认证，英文全称：Fair Labor Association，成立于1997年,是克林顿召集工人讨论成立的劳工监督机构，是一个非盈利性机构，致力于团结工业界、非政府组织和大学的力量，共同促进工作条件的改善和国际劳工标准的遵守。FLA 3.0是一个能力建设过程，在该模式下，工厂首先进行需求评估之后再进行影响分析。FLA的结果反映了工厂的实际情况，')}})]),_c('div',{staticClass:"text_div",attrs:{"data-v-2152299d":""}},[_c('span',{attrs:{"data-v-2152299d":""}},[_vm._v("了解更多")]),_c('img',{attrs:{"src":"data:image/png;base64,","alt":"图片"}})])])]),_c('div',{staticClass:"Certification_fors"},[_c('div',[_c('div',{staticClass:"certiscation_img"},[_c('img',{attrs:{"src":"https://vmcs.oss-cn-shenzhen.aliyuncs.com/project_img/1695108932770.png","alt":""}})]),_c('i'),_c('p',[_vm._v("TFS")]),_c('div',{staticClass:"div_html"},[_c('p',{domProps:{"innerHTML":_vm._s('&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp     TFS认证，化工供应链是全球一体化发展的结果，携手可持续发展（TfS）是一项由成员驱动的倡议，旨在提高整个化工行业的CSR标准。TFS成员是致力于使自身及其供应商的运营更具可持续性的化工企业，共同为化工供应链的环境、社会和管理绩效建立全球标准。TfS的目标是促进一个致力于气候保护的、安全的、合乎道德的化工行业。我们支持《联合国全球契约》和责任关怀®原则。TfS的理念是在全球化工行业内进行持续改进。通过评估或审计后触发的纠正行动计划，TFS成员企业的供应商正在持续改进他们的可持续发展绩效，并为同行设定标准。')}})]),_c('div',{staticClass:"text_div",attrs:{"data-v-2152299d":""}},[_c('span',{attrs:{"data-v-2152299d":""}},[_vm._v("了解更多")]),_c('img',{attrs:{"src":"data:image/png;base64,","alt":"图片"}})])])]),_c('div',{staticClass:"Certification_fors"},[_c('div',[_c('div',{staticClass:"certiscation_img"},[_c('img',{attrs:{"src":"https://vmcs.oss-cn-shenzhen.aliyuncs.com/project_img/1695108097331.png","alt":""}})]),_c('i'),_c('p