<template>
	<div :class="{'nav_main':true,'scroll':isVisable}">
		<!-- 顶部导航栏 -->
		<div class="head">
			<div class="header">
        <div class="logo">
          <img src="../assets/logo.webp" alt="">
        </div>
        <div class="nav">
          <div class="nav-box">
            <div class="nav-list">
              <div v-for="(item,index) of menuList" :key="index" :class="{'nav-item-hover':item.pathName==selectRoute}" @click="current(item.pathName,index)">
                <div class="nav-item">
                  <p>{{item.name}}</p>
                  <i></i>
                </div>
              </div>

            </div>

          </div>
        </div>
        <i class="idiv"></i>
        <div class="sea_photo">
          <div class="search_photo">
            <div class="search">
              <img src="../assets/search.png" alt="">
            </div>
            <img src="../assets/photo.png" alt="">
            <div class="photo">
              <p>400 690 0031</p>
            </div>
          </div>
        </div>
        <div>
          <div class="btn_list">
            <div class="span_list">
              <span>登录</span>
              <span>注册</span>
            </div>
          </div>
        </div>
			</div>
		</div>
	</div>
</template>

<script>
	export default {
        name:'HeadNav',
		data() {
			return {
        isVisable: false,
				path:'Home',
				menuList:[{
					name:'首页',
					pathName:'/Home'
				},{
					name:'服务项目',
					pathName:'/Example'
				},{
          name:'法律法规',
          pathName:''
        },{
          name:'Q&A',
          pathName:''
        },{
          name:'关于我们',
          pathName:''
        },{
          name:'联系我们',
          pathName:''
        }],
        selectRoute: '/Home'
			}
		},

		methods: {
      windowScrollListener() {
        var scrollTop = Math.floor(document.documentElement.scrollTop||document.body.scrollTop|| document.querySelector('.container').scrollTop)
        this.isVisable = scrollTop===0? false:true
      },
			current(path){
        this.selectRoute = path
				this.path = path
                if(this.path!==''){
                     this.$router.push(path);
                }else{
                    this.$router.push('/Home');
                }
        window.scrollTo({
          top: 0,
          behavior: 'smooth'
        });
				
			},
		},
    mounted() {
      // 获取当前路由对象
      this.selectRoute = this.$route.path;
    },
		created() {

          this.$nextTick(() => {
            window.addEventListener("scroll",this.windowScrollListener,true)
          })
		},
		
	}
</script>

