            <div>
              <div class="html_remark">
                <div class="Fanya_goods_SKT"><strong>什么是SA8000社会责任管理体系认证？</strong></div>
                <div class="Fanya_goods_SKT">&nbsp;</div>
                <div id="gtem1" class="Fanya_goods_txt">
                  <p>&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;SA8000即Social Accountability 8000，社会责任管理体系。
                    其主要内容包括：童工、强迫性或强制性劳动、健康与安全、结社自由和集体谈判的权利、歧视、惩戒性措施、工作时间、薪酬福利、管理体系。SA8000
                    衡量组织在工作场所社会责任重要方面的社会表现，同时配合使用管理体系要素来推进本标准在各方面的持续改进提升。本标准做法严格，确保在不影响商业利益的前提下让供应链达到最高标准的社会责任要求，因此备受各品牌及行业领军企业好评。</p>
                </div>
                <div class="Fanya_bsci_mb">
                  <div class="Fanya_bsci_mb_A" style="text-align: justify;">&nbsp; &nbsp; &nbsp; &nbsp;
                    随着社会的发展，人权意识的提高，社会责任终将会成为企业的重要竞争力之一；目前SA8000，认可程度相对比较高。获得SA8000证书的工厂，沃尔玛给与两年有效期。Tesco, Lidl
                    与Aldi等零售商给与三年有效期。
                  </div>
                  <div class="Fanya_bsci_mb_B">
                    <p class="Tindent">
                      我国早期的SA8000认证主要是在跨国公司采购商要求下进行的，认证数量较少，集中在东南沿海地区。随着世界各地对企业社会责任的呼声日益加大，SA8000对企业的经营活动和外贸出口的影响也开始显现。近几年，企业社会责任活动在中国达到新的高峰，越来越多的内陆企业也加入了认证行列。</p>
                    <p class="Tindent">&nbsp;</p>
                  </div>
                </div>
                <div class="Fanya_goods_ST"><strong>SA8000审核标准</strong></div>
                <div class="Fanya_goods_ST">&nbsp;</div>
                <div class="Fanya_goods_txt">&nbsp; &nbsp; &nbsp; &nbsp; SA
                  8000认证审核是依据该标准的要求审核、评价企业是否与保护员工基本权益的要求相符，在全球所有的工商领域均可应用和实施SA 8000体系，SA 8000认证审核范围包括：<br><br><img
                      src="http://www.fanya100.com/images/item/sa8000_02.png" width="815" height="186"></div>
                <div class="Fanya_goods_ST">&nbsp;</div>
                <div class="Fanya_goods_ST">&nbsp;</div>
                <div class="Fanya_goods_ST"><strong>影响SA8000结果的问题</strong></div>
                <div class="Fanya_goods_ST">&nbsp;</div>
                <div class="Fanya_goods_ST">&nbsp;</div>
                <ul>
                  <li>• 月加班超过36小时、周工时超过60小时<br>• 没有社保或者社保覆盖比例不足、但有持续的改善情况<br>• 工资与工作时间存在不一致，工资支付低于法规要求<br>•
                    营业执照与实际运营地不符或生产区域未覆盖营业执照范围内<br>• 宿舍与厂房、仓库三合一、安全出口数量不够<br>•
                    没有SPT社会绩效团队、有SPT社会绩效团队，但社会绩效团队及健康安全委员会未有效运行，比如社会绩效团队成员完全不清楚其职责及其参与的活动<br>• 员工未接受关于SA8000知识的任何培训
                  </li>
                </ul>
              </div>
            </div>
